.text-white {
    --bs-text-opacity: 1;
    color: rgba(var(--bs-white-rgb), var(--bs-text-opacity)) !important;
}

.fab {
    font-family: "Font Awesome 5 Brands";
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;

}

.footer_links {
    color: white;



}

a:link {
    text-decoration: none;
}


a:visited {
    text-decoration: none;
}


a:hover {
    text-decoration: none;
    color: rgb(23, 172, 226)
}


a:active {
    text-decoration: none;
}

.footerLink {
    font-weight: bold;
}

.fbicon {
    background-color: white;
    color: #39599f;

}

.fbiconParent {
    color: #39599f;
    background-color: white;
    border-radius: 50%;
    width: 10%;
    position: absolute;
    width: 15px;
    height: 15px;
}



.icon {
    background: #dee2e6;
    width: 15px;
    height: 15px;
    border-radius: 100%;
    position: absolute;
    top: 0em;
    left: 0em;
    
}

