.navSearch {
    margin-left: 600px;
}

.navbar {
    overflow: hidden;
    background-color: #333;
    position: fixed;
    /* Set the navbar to fixed position */
    top: 0;
    z-index: 9999;
    /* Position the navbar at the top of the page */
    width: 100%;
    /* Full width */
}

.navbar-toggler {
    display: none;
}

.navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.navbar a:hover {
    background: #ddd;
    color: black;
}


a {
    color: rgb(11, 10, 10);
    text-decoration: none;
    font-weight: bold;
}

.p-menu1 {
    height: 100%;
}




/* Hamburger */
.hamburger1 {
    height: 38px;
    margin: 2px;
    display: grid;
    grid-template-rows: repeat(133, 10fr);
    justify-items: center;
    z-index: 22;
}

.hamburger1 div {
    background-color: rgb(61, 61, 61);
    position: relative;
    width: 40px;
    height: 5px;
    margin-top: 7px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

#toggle1 {
    display: none;
}

#toggle1:checked+.hamburger1 .top {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    margin-top: 22.5px;
}

#toggle1:checked+.hamburger1 .meat {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    margin-top: -5px;
}

#toggle1:checked+.hamburger1 .bottom {
    -webkit-transform: scale(0);
    transform: scale(0);
}

#toggle1:checked~.menu1 {
    height: 340px;


}

.hamburger1 {
    cursor: pointer;
}



/* Menu */
.menu1 {
    width: 100%;
    background-color: #af9494;
    margin: 0;
    display: -ms-grid;
    display: grid;
    grid-template-rows: 1fr repeat(4, 0.5fr);
    grid-row-gap: 25px;
    padding: 0;
    list-style: none;
    clear: both;
    width: auto;
    text-align: center;
    height: 0px;
    overflow: hidden;
    transition: height .4s ease;
    z-index: 120;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;


}



.menu1 a:first-child {
    margin-top: 40px;
}

.menu1 a:last-child {
    margin-bottom: 40px;
}



.link1 {
    width: 100%;
    margin: 1px;
    padding: 4px 0;
    font: 587 15px 'Oswald', sans-serif;
    color: black;

}

.link1:hover {
    background-color: rgb(211, 97, 10);
    color: rgb(160, 10, 10);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}


form.example input[type=text] {
    padding: 10px;
    font-size: 17px;
    border: 1px solid grey;
    float: left;
    width: 80%;
    background: #f1f1f1;
}

form.example button {
    float: left;
    width: 20%;
    padding: 10px;
    background: #2196F3;
    color: white;
    font-size: 17px;
    border: 1px solid grey;
    border-left: none;
    cursor: pointer;
}

form.example button:hover {
    background: #0b7dda;
}

form.example::after {
    content: "";
    clear: both;
    display: table;
}